.nav {
  padding: 20px 0;
  background-color: var(--dark-bg);
  border: 1px solid var(--dark-border);
  font-family: "Poppins", sans-serif;
  letter-spacing: normal;
  color: var(--header-text);
}

.nav-row {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  -moz-column-gap: 30px;
  column-gap: 30px;
  row-gap: 20px;
  flex-wrap: wrap;
}

.logo {
  margin-right: auto;
  color: var(--header-text);
  font-size: 24px;
  font-weight: 400;
}

.nav-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  -moz-column-gap: 40px;
  column-gap: 40px;
  row-gap: 10px;
  font-size: 16px;
  font-weight: 500;
  transition: opacity 0.2s ease-in;
}
.nav-list__link {
  color: var(--header-text);
  transition: opacity 0.2s ease-in;
}
.nav-list__link:hover {
  opacity: 0.8;
}
.nav-list__link-active {
  position: relative;
}
.nav-list__link-active::before {
  content: "";
  position: absolute;
  left: 0;
  top: 100%;
  display: block;
  height: 2px;
  width: 100%;
  background-color: var(--accent);
}
